const Game = require('./game.js');

class View {

  constructor($el) {
    this.$el = $el;
    this.string = "";
    this.wordForm = $(".word-form");
    let dictionary;
    $.get("https://s3-us-west-1.amazonaws.com/virginia-wordgame/AllWords.txt", (txt) => {
      dictionary = txt.split('\n');
    }).then((dict) => {
      this.game = new Game(dictionary);
      this.setupBoard();
    });

    this.interval = window.setInterval(
      this.step.bind(this), View.STEP_MILLIS
    );

    $(".string").focus();

    this.gameOver = document.getElementById("gameOver");


    // $(".string").change((e) => this.handleCharacter(e));
    $( "#word-form" ).submit((e) => this.handleSubmit(e));

    $(window).on("keydown", this.handleEnter.bind(this));
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.step = this.step.bind(this);

    // this.handleCharacter = this.handleCharacter.bind(this);

  }

  // submit the form if Enter is pressed
  handleEnter(e) {
    if (e.keyCode == 13) {
      if (document.getElementById("game-over") === null) {
        e.preventDefault();
        $( "#word-form" ).submit();
      } else {
        e.preventDefault();
        location.reload();
      }
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    this.string = $( "input:first" ).val().toUpperCase();
    if (this.game.board.validWord(this.string)) {
      this.game.handleWord(this.string);
      this.render();
      this.string = "";
      let form = document.getElementById("word-form");
      form.reset();
      let $score = document.getElementById("score");
      $($score).attr('data-score', this.game.score);
    } else {
      this.game.badWord.play();
    }
  }

  handleCharacter(e) {
    e.preventDefault();
    this.string = e.target.value;
    // check if the new character is in the list of letters on the screen.
    // If it is, turn that tile green. If it's not, give an alert/indicator
    let lastChar = this.string.charAt(this.string.length-1);
    this.board.handleLetter(lastChar);
  }

  setupBoard() {
    // setting up the tiles grid
    const $ul = $("<ul>");
    $ul.attr('id','all-tiles');

    for (let rowIdx = 0; rowIdx < 3; rowIdx++) {
      for (let colIdx = 0; colIdx < 10; colIdx++) {
        let $li = $("<li>");
        $li.data("pos", [rowIdx, colIdx]);
        if (this.game.board.grid[rowIdx][colIdx]) {
          $li.attr('data-letter', this.game.board.grid[rowIdx][colIdx].letter);
          $li.addClass("tile");
        }

        $ul.append($li);
      }
    }

    $(this.$el).append($ul);

    // adding the bar below the tiles
    const $ledge = $("<div>");
    $ledge.attr('id','ledge');
    $(this.$el).append($ledge);

    // introductory box
    const $intro = $("<div><h1>INSTRUCTIONS</h1><p>Create words out of the letter tiles on the screen. Use the letters before they reach the left edge! Longer words earn you more points.</p></div>");
    $intro.attr('id','intro');
    const $start = $("<button id='start'>START</button>");
    $start.click(this.startGame);
    $intro.append($start);
    $(this.$el).append($intro);
  }


  startGame() {
    let $intro = document.getElementById("intro");
    $intro.remove();
  }

  render() {
    let $ul2 = document.getElementById("all-tiles");
    $ul2.remove();

    const $ul = $("<ul>");
    $ul.attr('id','all-tiles');
    for (let rowIdx = 0; rowIdx < 3; rowIdx++) {
      for (let colIdx = 0; colIdx < 10; colIdx++) {
        let $li = $("<li>");
        $li.data("pos", [rowIdx, colIdx]);
        if (this.game.board.grid[rowIdx][colIdx]) {
          $li.attr('data-letter', this.game.board.grid[rowIdx][colIdx].letter);
          $li.addClass("tile");
        }

        $ul.append($li);
      }
    }
    $(this.$el).append($ul);

  }

  step() {
    let $intro = document.getElementById("intro");
    if (!$intro) {
      this.game.board.incrementTiles();
      this.render();
      if (this.game.lose()) {
        this.displayLosing();
      }
    }
  }

  displayLosing() {
    clearInterval(this.interval);
    this.gameOver.play();

    let $form = document.getElementById("word-form");
    $form.remove();

    const $ul = $("<ul>");
    $ul.attr('id','game-over');
    $ul.text("GAME OVER");

    const $replay = $("<div><button id='replay' onclick='location.reload()'>PLAY AGAIN!</button></div>");
    $ul.append($replay);

    $(this.$el).append($ul);
  }

}

View.STEP_MILLIS = 2000;

module.exports = View;



